@import '../../styles/variables.scss';

.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: var(--beige);

    &__spinner {
        border: 16px solid var(--light-grey);
        border-top: 16px solid var(--aqua);
        border-radius: 50%;
        width: 120px;
        height: 120px;
        animation: spin 2s linear infinite;
    }

    &__loading-text {
        margin-top: 20px;
        font-size: 1.5rem;
        color: var(--dark-grey);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
